import { Link } from 'react-router-dom/cjs/react-router-dom';
import styles from './navbar.module.css'

function Navbar({handleChange, handleSubmit}) {
    return (
        <div className={styles}>
            
            <Link to='/'>
                <button className='btn'>LANDING</button>
            </Link>

            <Link to='/home'>
            <button className='btn' onClick={handleSubmit}>REFRESH</button>
            </Link>

            <Link to='/create'>
            <button className='btn'>CREAR POKEMON</button>
            </Link>
            
            <input type='search' className='input' placeholder='Buscar...' onChange={handleChange}/>
            {/* <button type='submit' className='btn' onClick={handleSubmit}>BUSCAR</button> */}

        </div>
    );
}

export default Navbar;